.register-submit{
    /* Frame 2 */

    /* Auto layout */
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 10px 30px;
    gap: 10px;

    /*width: 125px;*/
    height: 47px;

    background: linear-gradient(90deg, #FF9900 0%, #FF6B00 100%);
    box-shadow: 2px 4px 6px rgba(0, 0, 0, 0.25);
    border-radius: 50px;
    border: none;

    /* Inside auto layout */
    flex: none;
    flex-grow: 0;

    font-family: 'Sen', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 27px;

    color: #FFFFFF;


    /* Inside auto layout */
    flex: none;

    flex-grow: 0;

}

.register-submit:active{
    box-shadow: none;
    transform: translate(4px, 4px);
}


